import React, { Component } from 'react'
import Axios from 'axios'
import WarningIcon from '@material-ui/icons/Warning'
import Footer from './Footer'
import emailjs from 'emailjs-com'
import '../App.css'

class Body extends Component {
    constructor(props) {
        super(props);
        this.state = {
            emailid: "",
            isValid: false,
            userscount: 0
        };
        this.getUsersCount();
        this.handleChange = this.handleChange.bind(this)
        this.handleClick = this.handleClick.bind(this)
        this.getUsersCount = this.getUsersCount.bind(this);
        this.handleInsert = this.handleInsert.bind(this);
    }

    handleChange = (e) => {
        if(this.validateEmail(e.target.value)){
            this.setState({
                isValid: true
            })
        }
        else{
            this.setState({
                isValid: false
            })
        }
    }

    handleClick = (e) => {
        e.preventDefault();
        this.setState({
            emailid: document.getElementById('emailid').value
            }, () => {
                this.handleInsert();
        });   
        
    }

    getUsersCount = () => {
        // fetch("https://newsletter-backend-api.herokuapp.com/api/userscount")
        //     .then(response => response.json())
        //     .then(data => {
        //         this.setState({
        //             userscount: data[0]['cnt']
        //         })
        //     });
        Axios.get("https://newsletter-backend-api.herokuapp.com/api/userscount")
            .then(response => {
                this.setState({
                    userscount: response.data[0]['cnt']
                })                
            }).catch((err) => {
                console.log(err);
            })
    }

    validateEmail(email){
        var pattern1 = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern1.test(email)) {
            return false;
        }

        var pattern2 = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
        if (!pattern2.test(email)) {
            return false;
        }
        
        return true;
    }

    sendEmail = (userEmail) => {
        var templateParams = {
            user_emailid: userEmail,
        };
        console.log('send email part')
        emailjs.send('service_cfbw02c', 'template_7edu23h', templateParams, 'user_4m8k6quWiYDDnpEKCha1K')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
    }

    handleInsert() {
        Axios.get("https://newsletter-backend-api.herokuapp.com/api/insert?emailid="+this.state.emailid)
        .then(response => {
            if(response.data[0]["res"] == 0)
            {
                alert('Thanks for your Love!\nYou have already subscribed!!!');
            }
            else if(response.data[0]["res"] == 1)
            {
                this.sendEmail(this.state.emailid);
                this.getUsersCount();
                alert('Subscription Successful!');
            }
            
        }).catch((err) => {
            console.log(err);
        })
    }

    render() {
        return (
            <div>
                <div className="main">
                    <div className="p">
                        <div className="txt">
                            <div style={{ fontSize: "20px" }}><i><b>Preparing for interviews?</b></i><br /></div>
                            Subscribe to this newsletter and
                            get daily practice problems to get <b>One Step Closer</b>
                        </div>
                        <input id="emailid" type="email" name="name" style={{ width: '70%', textAlign: 'center' }}
                            onChange={this.handleChange} autoComplete='off' placeholder="Enter your email..." />
                        <br />
                        <button style={{ width: '70%', backgroundColor: '#9ACA27' }} onClick={this.handleClick} disabled={!this.state.isValid}>Subscribe Now!</button>
                        <div style={{ color: '#ffee5b', display: this.state.isValid ? 'none' : 'block' }}><WarningIcon /> Please enter a valid Email Address!</div>
                    </div>
                    <div className="content">
                        <ul style={{ listStyleType: 'none' }}>
                            <li>
                                Daily an email is sent, each mail consists of 6 leetcode problems
                            </li>
                            <li>
                                The difficulty level of Problems keeps increasing as days pass by
                            </li>
                            <li>
                                You don't need a leetcode subscription to solve these challenges
                            </li>
                        </ul>
                    </div>
                </div>
                <footer className="footer"><Footer usercount={this.state.userscount}/></footer>
            </div>
        )
    }
}

export default Body;